<template>
    <v-app style="user-select: none; background: #f1f1f1;">
        <Menu :items="items" v-model="selectedItem"></Menu>
        <TopBar><strong>LAN</strong> Firemaker <i style="font-size:0.6em"> BETA</i></TopBar>

        <v-main>
            <v-container style="padding-top:30px">
                <!--<v-row>-->
                    <!--justify="center"-->
                    <!--align="center">-->
                    <transition name="component-fade" mode="out-in">
                        <keep-alive>
                            <component :is="getContentComponent"></component>
                        </keep-alive>
                    </transition>
                <!--</v-row>-->
            </v-container>
        </v-main>
    </v-app>
</template>

<script>
    import Menu from './components/Menu';
    import TopBar from './components/TopBar';
    import Welcome from './content/Welcome';
    import Logs from './content/Logs';

    export default {
        name: 'App',

        components: {
            Menu,
            TopBar,
            Welcome,
            Logs,
        },

        computed: {
            getContentComponent() {
                return this.selectedItem ? this.selectedItem.component : 'Welcome';
            }
        },

        mounted: function() {

            var self = this;

            var int = setInterval(function() {
                if (window.profileManager) {

                    clearInterval(int);

                    var lastSettings = window.profileManager.loadSettings("last_settings");

                    if (lastSettings) {
                        console.log("Last settings: "+lastSettings);

                        var obj = JSON.parse(lastSettings);
                        self.$store.replaceState(obj);
                    }
                }
            }, 100);
        },

        data() {

            return {
                items: [
                    {text: 'Welcome', icon: 'mdi-human-greeting', component: 'Welcome'},
                    {text: 'Logs', icon: 'mdi-campfire', component: 'Logs'},
                    // {text: 'Wintertodt', icon: 'mdi-snowflake', component: 'Welcome'},
                    // // {text: 'Chests', icon: 'mdi-treasure-chest', component: 'Welcome'},
                    // // {text: 'Stalls', icon: 'mdi-store', component: 'Welcome'},
                    // // {text: 'Blackjacking', icon: 'mdi-arm-flex', component: 'Welcome'},
                    // {text: 'Progressive Mode', icon: 'mdi-timeline-clock', component: 'Welcome'},
                    // {text: 'Automation', icon: 'mdi-robot-industrial', component: 'Welcome'},
                    // {text: 'Settings', icon: 'mdi-cog', component: 'Welcome'}
                ],

                selectedItem: null,
            };
        },
    };

</script>

<style scoped>

    .component-fade-enter-active, .component-fade-leave-active {
        transition: opacity .3s ease;
    }

    .component-fade-enter, .component-fade-leave-to {
        opacity: 0;
    }
</style>

