<template>
    <div>
        <v-item-group v-model="selected">
            <v-container>
                <v-row>
                    <v-col
                            v-for="item in items"
                            :key="item.id"
                            cols="12"
                            md="2"
                            style="padding:6px">
                        <v-item v-slot:default="{ active, toggle }" :value="item.id">
                            <v-card
                                    class="d-flex align-center"
                                    height="100"
                                    :img="require('../assets/' + item.image)"
                                    @click="toggle"
                                    style="margin-bottom:40px;"
                                    :style="opacity(active)">
                                <v-card-text>
                                    <div class="subtitle-1 text-center"
                                         :class="active ? 'font-weight-bold green--text': ''"
                                         style="margin-top: 130px;">{{item.name}}
                                    </div>
                                </v-card-text>

                                <v-scroll-y-transition>
                                    <div v-if="active" class="display-3 flex-grow-1 text-center"
                                         style="position: absolute;left: 29%;top:7%">
                                        <v-icon size="64" color="green">mdi-check</v-icon>
                                    </div>
                                </v-scroll-y-transition>
                            </v-card>
                        </v-item>
                    </v-col>
                </v-row>
            </v-container>
        </v-item-group>
    </div>
</template>

<script>
    export default {
        props: ['value', 'items', 'allowMulti'],
        name: "ActiveSelector",
        data() {
            return {
            };
        },
        computed: {
            selected: {
                get: function() {
                    return this.value ? this.value.id : null;
                },
                set: function(newVal) {
                    this.$emit('input', this.items.find(item => item.id === newVal));
                }
            }
        },
        methods: {
            opacity(active) {
                if (this.selected == null) {
                    return 'opacity:1;'
                }

                if (active) {
                    return 'opacity:1;'
                }
                return 'opacity:0.6;'
            }
        }
    }
</script>

