<template>
    <iframe class="elevation-5" style="width:100%;height:100%" :src="'https://laniax.github.io/explv-map/?centreX='+getX+'&centreY='+getY+'&centreZ='+getZ+'&zoom='+getZoom"></iframe>
</template>

<script>
    export default {
        props: ['round', 'x', 'y', 'z', 'zoom', 'location'],
        name: "RSMap",
        computed: {
            getX() {
                return this.location ? this.location.getVisibleLocation().x : this.x;
            },
            getY() {
                return this.location ? this.location.getVisibleLocation().y : this.y;
            },
            getZ() {
                return this.location ? this.location.getVisibleLocation().z : this.z;
            },
            getZoom() {
                return this.location ? this.location.getVisibleLocation().zoom : this.zoom;
            }
        },
    }
</script>

<style scoped>

    iframe {
        border: none;
        /** Note: JavaFX webview does NOT like a border-radius on the iframe! **/
    }

</style>