import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

import Log from '../entities/Log';
import Location from '../entities/Location';


Vue.use(Vuex);

const state = { // defaults

    logs: {
        log: null,
        location: null,
        customLocations: [],
    }

};


const getters = {
    currentLog: state => {
        return Log.fromSaveable(state.logs.log);
    },
    currentLocation: state => {
        return Location.fromSaveable(state.logs.location);
    },
    customLocations: state => {

        if (!state.logs.customLocations)
            return [];

        return state.logs.customLocations.map(x => Location.fromSaveable(x));
    }
};

const mutations = {
    updateLog(state, newLog) {
        state.logs.log = newLog ? newLog.toSaveable() : null;
    },
    updateLocation(state, newLocation) {
        state.logs.location = newLocation ? newLocation.toSaveable() : null;
    },
    addCustomLocation(state, newLocation) {
        state.logs.customLocations.push(newLocation.toSaveable());
    },
    removeCustomLocation(state, location) {
        const i = state.logs.customLocations.map(item => item.id).indexOf(location.id);
        state.logs.customLocations.splice(i, 1);
    },
};

export default new Vuex.Store({
    state,
    getters,
    mutations,
    plugins: [createPersistedState({
        storage: {
            getItem: (key) => {
                console.log("get item", key);
                return null;
            },
            setItem: (key, value) => {
                console.log("set item", key,value);
            },
            removeItem: (key) => {
                console.log("remove item", key);
            },
        },
    })]
});
