import Saveable from "../entities/Saveable"

export default class Location extends Saveable {

    constructor(data) {
        super();
        this.id = data.id;
        this.name = data.name;

        this.location = data.location;
        this.visibleLocation = data.visibleLocation;
    }

    toSaveable() {
        return this.id;
    }

    getVisibleLocation() {
        return this.visibleLocation || this.location;
    }

    static fromSaveable(value) {
        return AllLocations.find(location => location.id === value);
    }
}

