<template>
    <v-row>

        <v-col cols="9">
            <v-select
                    v-model="selected"
                    :items="items"
                    item-text="name"
                    item-value="id"
                    label="Select"
                    solo>
                <template v-slot:selection="data">
                    <v-list-item-title v-text="data.item.name"></v-list-item-title>
                </template>
                <template v-slot:item="data">
                    <v-list-item-title v-text="data.item.name"></v-list-item-title>
                </template>
            </v-select>

            <v-btn @click="add" class="float-right" color="secondary">
                <v-icon left>mdi-plus</v-icon>
                Add custom
            </v-btn>

        </v-col>
        <v-col v-if="selected" cols="3">
            <RSMap style="width:100%;height:103%;" :round="false" :location="selected"></RSMap>
        </v-col>
    </v-row>
</template>

<script>

    import RSMap from "../components/RSMap"

    export default {
        props: ['value', 'items'],
        name: "LocationSelector",
        components: {RSMap},
        methods: {
            add() {

            }
        },
        computed: {
            selected: {
                get: function () {
                    return this.value ? this.value : null;
                },
                set: function (newVal) {
                    this.$emit('input', this.items.find(item => item.id === newVal));
                }
            }
        },
    }
</script>

