import Saveable from "../entities/Saveable"

export default class Log extends Saveable {

    constructor(data) {
        super();
        this.id = data.id;
        this.name = data.name;
        this.image = data.image;
    }

    toSaveable() {
        return this.id;
    }

    static fromSaveable(value) {
        return AllLogs.find(log => log.id === value);
    }
}

