<template>
    <v-btn x-large @click="startScript" class="float-right" color="success" :disabled="disabled">{{label ? label : 'Start script'}}</v-btn>
</template>

<script>
    export default {
        props: ['mode', 'label', 'disabled'],
        name: "StartScriptButton",
        data() {
            return {};
        },
        methods: {
            startScript() {

                var state = JSON.stringify(this.$store.state);

                if (window.profileManager) {
                    window.profileManager.saveSettings("last_settings", state);
                }

                console.log("start script!: " + state);

                if (window.gui) {
                    window.gui.close();
                }
            }
        }
    }
</script>

