<template>
    <div>
        <v-row justify="center"
               align="center">
            <v-col cols="12">

                <v-card raised elevation="2">
                    <v-card-title>
                        Choose Logs
                    </v-card-title>

                    <v-card-text>
                        Please click on the type of logs you want to burn.<br><br>
                        If you want to automatically switch to other logs depending on your firemaking level, check out the 'Progressive Mode' in the menu.
                        <br><br>
                        <ActiveSelector v-model="log" :items="logs"></ActiveSelector>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>

        <v-row justify="center"
               align="center">
            <v-col cols="12">
                <v-card raised elevation="2">
                    <v-card-title>
                        Choose Location
                    </v-card-title>

                    <v-card-text>
                        Please select a location where you want to burn the logs.<br><br>
                        Is the location you want to use not in the list? Don't worry! You can save your own locations by clicking on the 'Add custom location' button!
                        <br><br>
                        <LocationSelector :items="locations" v-model="location"></LocationSelector>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>

        <v-row justify="center"
               align="center">
            <v-col cols="12">

               <StartScriptButton :disabled="!log || !location" mode="logs" label="Start burning logs"></StartScriptButton>

            </v-col>
        </v-row>
    </div>
</template>

<script>

    import ActiveSelector from '../components/ActiveSelector';
    import StartScriptButton from '../components/StartScriptButton';
    import {AllLogs} from '../entities/Log';
    import {AllLocations} from '../entities/Location';
    import LocationSelector from "../components/LocationSelector";

    export default {
        name: "Logs",
        components: {LocationSelector, ActiveSelector, StartScriptButton},
        data() {
            return {
                logs: AllLogs,
            }
        },
        computed: {
            log: {
                set(log) {
                    this.$store.commit("updateLog", log);
                },
                get() {
                    return this.$store.getters.currentLog;
                }
            },
            location: {
                set(location) {
                    this.$store.commit("updateLocation", location);
                },
                get() {
                    return this.$store.getters.currentLocation;
                }
            },
            locations() {
                return [...AllLocations, ...this.$store.getters.customLocations];
            }
        },
        methods: {
        }
    }
</script>

<style scoped>

</style>